/* ====================================== animation */
@keyframes bar-t {
  0% {
    transform: translate(0, 0) rotate(0);
  }
  50% {
    transform: translate(0, 8px) rotate(0);
  }
  100% {
    transform: translate(0, 8px) rotate(20deg);
  }
}
@keyframes bar-c {
  0% {
    opacity: 1;
  }
  49% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
@keyframes bar-b {
  0% {
    transform: translate(0, 0) rotate(0);
  }
  50% {
    transform: translate(0, -8px) rotate(0);
  }
  100% {
    transform: translate(0, -8px) rotate(-20deg);
  }
}
@keyframes bar-t-r {
  0% {
    transform: translate(0, 8px) rotate(20deg);
  }
  50% {
    transform: translate(0, 8px) rotate(0);
  }
  100% {
    transform: translate(0, 0) rotate(0);
  }
}
@keyframes bar-c-r {
  0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes bar-b-r {
  0% {
    transform: translate(0, -8px) rotate(-20deg);
  }
  50% {
    transform: translate(0, -8px) rotate(0);
  }
  100% {
    transform: translate(0, 0) rotate(0);
  }
}
@keyframes loop01 {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes loop02 {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes loop03 {
  0% {
    transform: translateX(200%);
  }
  100% {
    transform: translateX(100%);
  }
}
@keyframes loop01-r {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes loop02-r {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100%);
  }
}
@keyframes loop03-r {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(200%);
  }
}
@keyframes loop01-up {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100%);
  }
}
@keyframes loop02-up {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes loop03-up {
  0% {
    transform: translateY(200%);
  }
  100% {
    transform: translateY(100%);
  }
}
@keyframes loop01-down {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes loop02-down {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(100%);
  }
}
@keyframes loop03-down {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(200%);
  }
}
.loop-slider {
  overflow: hidden;
  font-size: 0;
  line-height: 0;
}
.loop-slider .slider {
  display: flex;
  flex-wrap: nowrap;
  position: relative;
}
.loop-slider .slider .list {
  display: flex;
  align-items: center;
}
.loop-slider .slider .list:nth-child(2) {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(100%);
}
.loop-slider .slider .list:nth-child(3) {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(200%);
}
.loop-slider .slider.-animation .list:nth-child(1) {
  animation: loop01 80s linear 0s infinite;
}
@media all and (-ms-high-contrast: none) {
  .loop-slider .slider.-animation .list:nth-child(1) {
    animation: none;
  }
}
.loop-slider .slider.-animation .list:nth-child(2) {
  animation: loop02 80s linear 0s infinite;
